  padding: 0;
}
.m_switcher__item {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #333;
}
.m_switcher__itemIcon {
  margin-right: 10px;
}
.m_switcher__trigger {
  color: #cdd0d2;
}
.m_switcher__icon {
  fill: currentColor;
}
@media screen and (min-width: 44rem) {
  .m_switcher {
    display: block;
    position: relative;
  }
  .m_switcher__items {
    position: absolute;
    background: white;
    border: 1px solid #9ba1a5;
    border-radius: 3px;
    right: 100%;
    margin-right: -24px;
  }
  .m_switcher__items:before {
    content: "";
    display: block;
    position: absolute;
    right: 8px;
    top: -8px;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-bottom: 8px solid #9ba1a5;
  }
  .m_switcher__item {
    padding: 0.625rem;
    background-color: attr(data-colour-1);
  }
  .m_switcher__item:hover {
    background-color: #9ba1a5;
  }
}
.m_switcher__item {
  cursor: pointer;
}
.m_collection {
  border-radius: 5px;
  background-color: white;
  margin-bottom: 1.9375rem;
  box-shadow: 0 1px 2px #b3b3b3;
  overflow: hidden;
}
.m_collection__title {
  margin: 0;
  font-size: 1em;
  border-bottom: 1px solid #e6e6e6;
}
.m_collection__titleLink {
  color: #05131F;
  padding: 0.484375rem;
  text-decoration: none;
  display: block;
  border-radius: 5px 5px 0 0;
  background-color: #fafafa;
}
.m_collection__titleLink:before {
  content: "+ View ";
}
@media screen and (min-width: 44rem) {
  .m_collection__titleLink:before {
    content: "";
  }
}
.m_collection:target .m_collection__titleLink:after {
  content: "";
}
.m_collection__items {
  margin: 0;
  padding: 0;
  display: none;
}
@media screen and (min-width: 44rem) {
  .m_collection__items {
    display: block;
  }
}
.m_collection:target .m_collection__items {
  display: block;
}
.m_collection__item {
  list-style: none;
  border-bottom: 1px solid #e6e6e6;
}
.m_collection__item:last-child {
  border: none;
}
.m_collection__link {
  padding: 0.484375rem;
  display: block;
  color: #05131F;
  text-decoration: none;
  border-left: 0.484375rem solid transparent;
}
.m_collection__link:hover {
  border-color: #26A2FF;
}
.c_page__content {
  padding-left: 1.9375rem;
  padding-right: 1.9375rem;
  padding-top: 1.9375rem;
  padding-bottom: 1.9375rem;
}
@media screen and (min-width: 75.25rem) {
  .c_page__content {
    padding-left: 0;
    padding-right: 0;
    margin: 0 auto;
    max-width: 73.3125rem;
  }
}
.c_page--prose {
  background-color: white;
}
.c_page--patterns .c_page__content {
  padding-left: 0;
  padding-right: 0;
}
@media screen and (min-width: 75.25rem) {
  .c_page--patterns .c_page__content {
    padding-left: 0;
    padding-right: 0;
    margin: 0;
    max-width: none;
  }
}
.c_header {
  padding-left: 1.9375rem;
  padding-right: 1.9375rem;
}
@media screen and (min-width: 75.25rem) {
  .c_header {
    padding-left: 0;
    padding-right: 0;
    margin: 0 auto;
    max-width: 73.3125rem;
  }
}
@media screen and (min-width: 44rem) {
  .c_header--home .c_header__content {
    padding-left: 0;
    padding-right: 0;
    margin: 0 auto;
    max-width: 43.375rem;
  }
}
.c_header__content {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.c_header__content > *:first-child {
  margin-right: auto;
}
.c_fullscreen {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #05131F;
  overflow-y: auto;
  z-index: 100;
  display: none;
  opacity: 1;
  transition: visibility 300ms cubic-bezier(1, 0.005, 0.845, 0.96), opacity 300ms cubic-bezier(1, 0.005, 0.845, 0.96);
}
.c_fullscreen:target {
  display: block !important;
}
.c_fullscreen--is-active {
  display: block !important;
}
.c_fullscreen--is-closed {
  visibility: hidden;
  opacity: 0;
}
.c_fullscreen--is-open {
  visibility: visible;
  opacity: 1;
}
@supports (display:flex) {
  .c_grid--double > * {
    margin-bottom: 1.25rem;
  }
  @media screen and (min-width: 44rem) {
    .c_grid--double {
      display: flex;
      flex-wrap: wrap;
    }
    .c_grid--double > * {
      flex-basis: 33.33333333%;
      flex-grow: 2;
    }
    .c_grid--double > *:nth-child(4n-2) {
      margin-left: 1.25rem;
    }
    .c_grid--double > *:nth-child(4n) {
      margin-left: 1.25rem;
    }
  }
  @media screen and (min-width: 75.25rem) {
    .c_grid--double > *:nth-child(4n-2) {
      flex-basis: 50%;
    }
    .c_grid--double > *:nth-child(4n-1) {
      flex-basis: 50%;
    }
  }
}
@media screen and (min-width: 44rem) {
  .c_grid--single {
    padding: 0;
    margin: 0 auto;
    max-width: 44rem;
  }
}
@media screen and (min-width: 44rem) {
  .c_grid--prose {
    padding-left: 0;
    padding-right: 0;
    margin: 0 auto;
    max-width: 43.375rem;
  }
}
