@supports (display:grid) or (display:-ms-grid) {
  @media screen and (min-width: 44rem) {
    .c_inventory__content {
      display: grid;
      grid-template-columns: 1fr 3fr;
      grid-column-gap: 1.9375rem;
    }
  }
  .c_inventory__content .c_inventory__toc,
  .c_inventory__content .c_inventory__items {
    float: none;
    width: auto;
  }
}
@media screen and (min-width: 44rem) {
  .c_inventory__toc {
    width: 20%;
    float: left;
  }
  .c_inventory__items {
    width: 70%;
    float: right;
  }
}
